

#result th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background: linear-gradient(180deg, #307e6c, #2b996d);
    color: white;
  }

  #result td{
    text-align: right;
  }

  #result {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
    max-width: 50rem;
     margin: 2rem auto;
     padding: 1rem;
    table-layout: fixed;
    border-spacing: 1rem;
    text-align: right;
  }
  
  #result td, #result th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  #result tr:nth-child(even){background-color: #005a3f;}
  
  #result tr:hover {background-color: #0f0000;}